.slider {
    background: #e4e6eb;
    border: 0;
    height: 20px;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    border-radius: 50em;
    position: relative;
    z-index: 10;
    margin: 0 18px!important;
}
.slider:after {
    background: #e4e6eb;
    content: '';
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    padding: 0 19px;
    left: -19px;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    border-radius: 50em;
    overflow: hidden;
}
.slider .ui-slider-range {
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    border-radius: 50em;
    position: absolute;
    padding: 0 19px 0 19px;
    margin: 0 -19px 0 -19px;
}
.ui-slider-handle {
    border: 0!important;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    border-radius: 50em;
    outline: 0!important;
}
.ui-slider-horizontal .ui-slider-handle {
    background: url("../../../../../../Xampp/htdocs/img/handle.png") no-repeat center center scroll #fff!important;
    background-position: 50% 50%;
    background-size: 16px 16px!important;
    width: 36px;
    height: 18px;
}
.ui-slider-horizontal .ui-slider-handle {
    top: 1px;
    margin-left: -18px;
}
.sliderVertical {
    background: #e4e6eb;
    border: 0;
    top: auto;
    bottom: auto;
    width: 20px!important;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    border-radius: 50em;
    -webkit-box-shadow: inset 0 5px 6px rgba(0, 0, 0, .05);
    -moz-box-shadow: inset 0 5px 6px rgba(0, 0, 0, .05);
    box-shadow: inset 0 5px 6px rgba(0, 0, 0, .05);
    position: relative;
    float: left;
    height: 100px;
    margin: 38px 20px 0 20px;
    width: 5px;
}
.sliderVertical:after {
    content: '';
    background: #e4e6eb;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 38px 0 0 0;
    top: -38px;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    border-radius: 50em;
    overflow: hidden;
}
.sliderVertical .ui-slider-range {
    height: 100%;
    width: 100%;
    top: auto;
    bottom: auto;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    border-radius: 50em;
    padding-top: 38px;
    margin-top: -38px;
}
.sliderVertical .ui-slider-handle {
    background: url("../../../../../../Xampp/htdocs/img/handlev.png") no-repeat center center scroll #fff!important;
    background-position: 50% 50%;
    background-size: 16px 16px!important;
    width: 18px;
    height: 36px;
    left: 50%;
    margin: 0 0 1px -9px!important;
}
.sliderVertical .ui-slider-range-max {
    top: 0;
}
.sliderVertical .ui-slider-range-min {
    bottom: 0;
}
