.wizard {
    *zoom: 1;
    padding-bottom: 20px;
    border-bottom: 1px solid #e4e6eb;
    margin-bottom: 20px;
}
.wizard:before, .wizard:after {
    display: table;
    content: "";
    line-height: 0;
}
.wizard:after {
    clear: both;
}
.wizard ul {
    list-style: none outside none;
    padding: 0;
    margin: 0;
}
.wizard ul li {
    float: left;
    margin: 0;
    padding: 0 20px 0 30px;
    height: 46px;
    line-height: 46px;
    position: relative;
    color: #c7cbd5;
    font-size: 16px;
    cursor: default;
}
.wizard ul li .chevron {
    display: none;
}
.wizard ul li:after {
    position: absolute;
    content: '';
    z-index: -1;
    width: 100%;
    height: 4px;
    background: #e4e6eb;
    left: 0;
    top: 50%;
    margin-top: 0;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px;
}
.wizard ul li:before {
    position: absolute;
    content: '';
    z-index: -1;
    width: 100%;
    height: 8px;
    background: #f6f6f8;
    left: -2px;
    top: 50%;
    padding: 0 2px;
    margin-top: -2px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
}
.wizard ul li.complete {
    color: #bdea74;
}
.wizard ul li.complete:hover {
    cursor: pointer;
}
.wizard ul li.complete:after {
    background: #bdea74;
}
.wizard ul li.complete .badge {
    border: 1px solid #bdea74;
    background: #bdea74;
    color: #fff;
}
.wizard ul li.active {
    color: #36a9e1;
}
.wizard ul li.active .badge {
    border: 1px solid #36a9e1;
    background: #fff;
    color: #383e4b;
}
.wizard ul li .badge {
    position: relative;
    margin-right: 8px;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    border-radius: 50em;
    font-size: 12px;
    padding: 10px;
    width: 14px;
    text-align: center;
    border: 1px solid #e4e6eb;
    background: #fff;
    color: #383e4b;
}
.wizard ul li .badge:before {
    position: absolute;
    content: '';
    z-index: -1;
    width: 100%;
    height: 100%;
    padding: 6px;
    background: #f6f6f8;
    top: -6px;
    left: -6px;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    border-radius: 50em;
}
.wizard ul li:nth-child(1) {
    border-radius: 4px 0 0 4px;
    padding-left: 20px;
    z-index: 10;
}
.wizard ul li:nth-child(2) {
